* {
  box-sizing: border-box;
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: Roboto;
}

body {
  background: radial-gradient(circle, #b44824ce 0%, #f0c512 78%);
  padding: 10px;
}

.header {
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  width: 100%;
  display: flex;
}

.header-titile {
  text-align: center;
  margin-top: 20px;
}

nav {
  width: 100%;
}

.header-nav {
  background-color: #ff7f2a;
  border-radius: 30px;
  justify-content: space-between;
  width: 100%;
  padding: 5px;
  list-style: none;
  display: flex;
}

a:hover {
  color: #fff;
  font-weight: bold;
}

a, a:visited {
  text-decoration: none;
}

.container-card {
  background-color: #ff7f2a;
  align-items: center;
  column-gap: 5px;
  margin: 20px;
  display: flex;
}

.container-img {
  width: 100px;
  height: 100px;
}

.container-information {
  padding: 5px;
}

footer {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
  position: fixed;
  bottom: 0;
}
